import React, {
  FC,
  memo,
  useEffect,
  useState,
  useCallback,
  SyntheticEvent,
} from 'react';
import classnames from 'classnames/bind';
import { useSelector, useDispatch } from 'react-redux';

import AccountHealth from 'components/AccountHealth';
import SectionTitle from 'components/SectionTitle';
import Modal from 'components/Modal';
import InternetSpeedTest from 'components/InternetSpeedTest';
import ConfirmModal from 'components/ConfirmModal';
import TabPanel from 'components/TabPanel';
import { changeUserInfoAction } from 'store/account/actions';

import { TimezoneType } from 'types/common';
import { getAccountInfo, getAccountInfoLoading } from 'store/account/selectors';
import avatarEmptySrc from 'assets/avatar-empty.png';

import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import Button from 'components/Button';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import ChangePasswordModal from 'components/ChangePasswordModal';

import styles from './styles.module.scss';

const cx = classnames.bind(styles);

const tabIds = {
  windows: 0,
  macos: 1,
  ios: 2,
  android: 3,
};

const BASE_POLICIES_PATH = 'https://www.novakidschool.com/terms_applications/';

const ProfileInformation: FC = () => {
  const [internetSpeedTestModalOpen, setInternetSpeedTestModalOpen] = useState(false);
  const [testIsRunning, setTestIsRunning] = useState(false);
  const [isChangePasswordModalVisible, setIsChangePasswordModalVisible] = useState(false);
  const [isTechRecommendationVisible, setIsTechRecommendationVisible] = useState(false);
  const [confirmModalOpen, setConfirmModalOpen] = useState(false);
  const [deviceTestModalOpen, setDeviceTestModalOpen] = useState(false);
  const [timezone, setTimezone] = useState<TimezoneType | null>(null);
  const dispatch = useDispatch();
  const [tab, setTab] = useState(tabIds.windows);

  const isLoading: boolean = useSelector(getAccountInfoLoading);
  const {
    userInfo,
    teacherInfo,
    regionInfo,
    timezones,
  } = useSelector(getAccountInfo);

  useEffect(() => {
    if (teacherInfo?.timezone) {
      const newTimezone = timezones.find((tz) => tz.id === teacherInfo.timezone);

      if (newTimezone) {
        setTimezone(newTimezone);
      } else {
        setTimezone(null);
      }
    } else {
      setTimezone(null);
    }
  }, [timezones, teacherInfo]);

  const faqLink = 'https://docs.google.com/document/d/11qBfUBRUv0eI5P5TBp-C0eOk74Cv7rbf5QeA0dSSKwU/edit?usp=sharing';
  const dpaLink = `${BASE_POLICIES_PATH}data-processing-addendum-for-teachers.pdf`;
  const billingLink = (
    teacherInfo?.teacher_is_near_native
      ? `${BASE_POLICIES_PATH}novakid-billing-policy-near-native-edition.pdf`
      : `${BASE_POLICIES_PATH}novakid-billing-policy-native-edition.pdf`
  );

  const getGeneralPolicyLink: () => string = () => {
    if (userInfo?.teacher_is_candidate) {
      return `${BASE_POLICIES_PATH}general-terms-and-conditions-for-applicants.pdf`;
    }

    if (teacherInfo?.teacher_is_near_native) {
      return `${BASE_POLICIES_PATH}general-terms-and-conditions-for-teachers-near-native-edition.pdf`;
    }

    return `${BASE_POLICIES_PATH}general-terms-and-conditions-for-teachers-native-edition.pdf`;
  };

  const handleTabChange = (event, newValue: number) => {
    setTab(newValue);
  };

  const handleToggleConfirmModal = useCallback(() => {
    setConfirmModalOpen((prevOpen) => !prevOpen);
  }, []);

  const handleToggleInternetSpeedTestModal = useCallback((event?: SyntheticEvent) => {
    event?.preventDefault();

    if (testIsRunning) {
      handleToggleConfirmModal();
      return;
    }
    setInternetSpeedTestModalOpen((prevOpen) => !prevOpen);
  }, [handleToggleConfirmModal, testIsRunning]);

  const handleToggleRunningTest = useCallback(() => {
    setTestIsRunning((prevState) => !prevState);
  }, []);

  const confirmModalCallback = useCallback(() => {
    if (internetSpeedTestModalOpen) {
      setInternetSpeedTestModalOpen(false);
    }

    if (deviceTestModalOpen) {
      setDeviceTestModalOpen(false);
    }

    setTestIsRunning(false);
    handleToggleConfirmModal();
  }, [deviceTestModalOpen, handleToggleConfirmModal, internetSpeedTestModalOpen]);

  const toggleShowingChangePasswordPopUp = (event?: SyntheticEvent) => {
    event?.preventDefault();

    setIsChangePasswordModalVisible(!isChangePasswordModalVisible);
  };

  const toggleShowingTechRecommendation = (event?: SyntheticEvent) => {
    event?.preventDefault();

    setIsTechRecommendationVisible(!isTechRecommendationVisible);
  };

  const onTimezoneFieldChange = (event, value: TimezoneType | null) => {
    setTimezone(value);

    if (value) {
      dispatch(
        changeUserInfoAction({
          timezone: value.id,
        }),
      );
    }
  };

  let period = '09:00-20:30 MSK';

  if (regionInfo) {
    const time = regionInfo.support_time_string || `${regionInfo.working_start_time}-${regionInfo.working_end_time}`;

    if (regionInfo.working_start_time) {
      period = `${time} ${regionInfo.timezone}`;
    }
  }

  let balance = 'N/A';

  if (userInfo?.balance) {
    const amount = new Intl.NumberFormat('en-IN', { style: 'currency', currency: 'USD' }).format(+userInfo.balance * +userInfo.class_cost);
    balance = `${userInfo.balance} classes (${amount})`;
  }

  return (
    <>
      <SectionTitle title="Profile information" loading={isLoading} bold />
      <div className={cx('profileuserInfo')}>
        <div className={cx('imgContainer')}>
          <img
            alt={teacherInfo?.name}
            src={teacherInfo?._links?.photo_content?.href || avatarEmptySrc}
            className={cx('img')}
          />
        </div>
        <div className={cx('userInfo')}>
          <h4 className={cx('name')}>
            {userInfo?.name || 'N/A'}
          </h4>
          <div className={cx('line')}>
            <strong>Your contacts: </strong>
            <a href={`mailto:${userInfo?.email || ''}`}>
              {userInfo?.email || 'N/A'}
            </a>
            {', '}
            <a href={`tel:${userInfo?.phone || ''}`}>
              {userInfo?.phone || 'N/A'}
            </a>
          </div>
          <div className={cx('line')}>
            <strong>Education: </strong>
            {userInfo?.education || 'N/A'}
          </div>
          <div className={cx('line')}>
            <strong>Experience: </strong>
            {teacherInfo?.biography || 'N/A'}
          </div>
          <div className={cx('line')}>
            <strong>Class cost: </strong>
            {userInfo?.class_cost ? `$${userInfo.class_cost}` : '$0.00'}
          </div>
          <div className={cx('line')}>
            <strong>Balance: </strong>
            {balance}
          </div>
          <div className={cx('line')}>
            <strong>Account health: </strong>
            <AccountHealth
              heartsLength={
                userInfo?.teacher_account_health ? parseInt(userInfo.teacher_account_health, 10) : 0
              }
            />
          </div>
          <div className={cx('line')}>
            <strong>Support team: </strong>
            Teacher support tel.
            {regionInfo?.support_phone && (
              <span>
                <a href={`tel:${regionInfo.support_phone}`}>
                  {' '}
                  {regionInfo.support_phone}
                  ,
                </a>
              </span>
            )}
            {regionInfo?.support_phone2 && (
              <span>
                <a href={`tel:${regionInfo.support_phone2}`}>
                  {' '}
                  {regionInfo.support_phone2}
                </a>
              </span>
            )}
            {period && (
              <span>
                {' '}
                {period}
              </span>
            )}
            {regionInfo?.support_email && (
              <span>
                {' '}
                E-Mail:
                <a href={`mailto:${regionInfo.support_email}`}>
                  {' '}
                  {regionInfo.support_email}
                </a>
              </span>
            )}
          </div>
          <div className={cx('line', 'timezone')}>
            <strong>Timezone: </strong>
            <div>
              <Autocomplete
                value={timezone}
                onChange={onTimezoneFieldChange}
                options={timezones}
                getOptionLabel={(option: TimezoneType) => (option ? option.name : 'N/A')}
                style={{ width: 300 }}
                renderInput={(params) => (
                  <TextField {...params} variant="outlined" size="small" />
                )}
              />
            </div>
          </div>
          <div className={cx('line', 'links')}>
            <strong>Useful links: </strong>
            <div>
              {userInfo?.group === 'teachers' && (
                <div>
                  <a
                    href={getGeneralPolicyLink()}
                    target="_blank"
                    rel="noreferrer"
                  >
                    General terms and conditions
                  </a>
                  {', '}
                  <a
                    href={faqLink}
                    target="_blank"
                    rel="noreferrer"
                  >
                    Teacher FAQ
                  </a>
                  {', '}
                  <a
                    href={billingLink}
                    target="_blank"
                    rel="noreferrer"
                  >
                    Billing policy
                  </a>
                  {', '}
                  <a
                    href={dpaLink}
                    target="_blank"
                    rel="noreferrer"
                  >
                    Data processing addendum
                  </a>
                </div>
              )}
              <div className={cx('modals')}>
                <a href="" onClick={toggleShowingChangePasswordPopUp}>Change password</a>
                {' | '}
                <a onClick={handleToggleInternetSpeedTestModal}>Internet speed test</a>
                {' | '}
                <a onClick={toggleShowingTechRecommendation}>Tech. recommendation</a>
                {' | '}
                <a onClick={toggleShowingChangePasswordPopUp}>Practice drawing</a>
              </div>
            </div>
          </div>
        </div>
      </div>
      <ChangePasswordModal
        onClose={toggleShowingChangePasswordPopUp}
        isOpen={isChangePasswordModalVisible}
      />
      <Modal
        title="Tech. recommendation"
        onClose={toggleShowingTechRecommendation}
        open={isTechRecommendationVisible}
      >
        <div className={cx('techRecommendation')}>
          <Paper square>
            <Tabs
              value={tab}
              indicatorColor="primary"
              textColor="primary"
              onChange={handleTabChange}
            >
              <Tab label="Windows OS" />
              <Tab label="macOs" />
              <Tab label="iOS" />
              <Tab label="Android" />
            </Tabs>
          </Paper>
          <TabPanel selectedId={tab} id={tabIds.windows}>
            <div className={cx('section')}>
              <ul>
                <li>
                  We recommend using the latest version of
                  {' '}
                  <a
                    href="http://chrome.google.com/"
                    target="_blank"
                    rel="noreferrer"
                  >
                    Google Chrome
                  </a>
                  {' '}
                  browser for classes.
                </li>
                <li>
                  <a href="" onClick={handleToggleInternetSpeedTestModal}>Check your equipment.</a>
                  {' '}
                  Be sure that you see and hear yourself.
                </li>
                <li>
                  {`If you have problems with sound or video: Check your computer for updates. 
                  (Start> Settings> Update and Security> Windows Update> Check for Updates.) 
                  Install them, if any, and restart the computer.`}
                </li>
                <li>
                  {`Refresh the page if you do not see / hear the teacher 
                  (you can use the keyboard shortcut Ctrl + F5).`}
                </li>
                <li>
                  {`Reboot the Wi-Fi router. Try to find a place in the room 
                  where the Wi-Fi signal is stronger.`}
                </li>
                <li>
                  <a href="" onClick={handleToggleInternetSpeedTestModal}>Take our internet speed test</a>
                  {` 10 minutes before the start of the lesson. We remind you that in the evening, 
                  Internet providers experience the greatest load on the network.`}
                </li>
                <li>
                  {`Check that during the lesson no one uses the Internet on the same communication channel 
                  (for example, on other computers at home), all third-party programs (skype, torrents) are disabled.`}
                </li>
              </ul>
            </div>
          </TabPanel>
          <TabPanel selectedId={tab} id={tabIds.macos}>
            <div className={cx('section')}>
              <div>
                <ul>
                  <li>
                    <span>
                      Important! We recommend using the latest
                      {' '}
                      <a
                        href="http://chrome.google.com/"
                        target="_blank"
                        rel="noreferrer"
                      >
                        Google Chrome
                      </a>
                      {' '}
                      browser for classes.
                    </span>
                  </li>
                  <li>
                    <a href="" onClick={handleToggleInternetSpeedTestModal}>Check your equipment.</a>
                    {' '}
                    <span>Be sure that you see and hear yourself.</span>
                  </li>
                  <li>
                    {`Check your computer for updates. Choose System Preferences from the Apple menu ()> 
                    Software Update> Update Now> Restart Computer.`}
                  </li>
                  <li>
                    {`Refresh the page if you do not see / hear the teacher 
                    (you can use the keyboard shortcut Cmd + R).`}
                  </li>
                  <li>
                    {`Reboot the Wi-Fi router. Try to find a place in the room 
                    where the Wi-Fi signal is stronger.`}
                  </li>
                  <li>
                    <a href="" ng-click="showInternetSpeedTestModal()">Take our internet speed test</a>
                    {` 10 minutes before the start of the lesson. We remind you that in the evening, 
                    Internet providers experience the greatest load on the network.`}
                  </li>
                  <li>
                    {`Check that during the lesson no one uses the Internet on the same communication channel 
                    (for example, on other computers at home), all third-party programs (skype, torrents) are disabled.`}
                  </li>
                </ul>
              </div>
            </div>
          </TabPanel>
          <TabPanel selectedId={tab} id={tabIds.ios}>
            <div className={cx('section', 'right')}>
              <ul>
                <li>Supported iOS 10.10+</li>
                <li><span>Use the Safari browser.</span></li>
                <li>
                  {`Make sure that all the latest updates are installed on the device. 
                  To do this, go to Settings> General> Software Update. Install them, if any, and restart the tablet.`}
                </li>
                <li>
                  {`Hold the tablet horizontally. In the tablet settings, 
                  the auto-rotate function must be enabled.`}
                </li>
                <li>Refresh the page if you do not see / hear the teacher.</li>
                <li>
                  Before starting classes, we recommend that you completely restart the device.
                </li>
                <li>
                  {`Reboot the Wi-Fi router. Try to find a place 
                  in the room where the Wi-Fi signal is stronger.`}
                </li>
                <li>
                  <a href="" onClick={handleToggleInternetSpeedTestModal}>Take our internet speed test</a>
                  {` 10 minutes before the start of the lesson. We remind you that in the evening, 
                  Internet providers experience the greatest load on the network.`}
                </li>
                <li>
                  {`Check that during the lesson no one uses the Internet on the same communication channel 
                  (for example, on other computers at home), all third-party programs (skype, torrents) are disabled.`}
                </li>
              </ul>
            </div>
          </TabPanel>
          <TabPanel selectedId={tab} id={tabIds.android}>
            <div className={cx('section')}>
              <ul>
                <li>
                  {`Use the latest version of the Google Chrome browser 
                  (you can update it in the Play Market app)`}
                </li>
                <li>
                  {`Make sure that all the latest updates are installed on your device. To do this, 
                    go to Settings> About Tablet or About Device> System Update. Free up space on your device, 
                    if necessary. Install updates and restart the tablet.`}
                </li>
                <li>
                  {`Hold the tablet horizontally. In the tablet settings, 
                  the auto-rotate function must be enabled.`}
                </li>
                <li>
                  Refresh the page if you do not see / hear the teacher.
                </li>
                <li>
                  Before starting classes, we recommend that you completely restart the device.
                </li>
                <li>
                  {`Reboot the Wi-Fi router. Try to find a place in 
                  the room where the Wi-Fi signal is stronger.`}
                </li>
                <li>
                  <a href="" onClick={handleToggleInternetSpeedTestModal}>Take our internet speed test</a>
                  {` 10 minutes before the start of the lesson. We remind you that in the evening, 
                  Internet providers experience the greatest load on the network.`}
                </li>
                <li>
                  {`Check that during the lesson no one uses the Internet on the same communication channel 
                  (for example, on other computers at home), all third-party programs (skype, torrents) are disabled.`}
                </li>
              </ul>
            </div>
          </TabPanel>
          <div className={cx('footer')}>
            <Button
              theme="primary"
              text="OK"
              className={cx('OK')}
              onClick={toggleShowingTechRecommendation}
            />
          </div>
        </div>
      </Modal>
      <Modal
        title="Internet speed test"
        onClose={handleToggleInternetSpeedTestModal}
        open={internetSpeedTestModalOpen}
      >
        <InternetSpeedTest
          toggleRunningTest={handleToggleRunningTest}
          finishTest={handleToggleInternetSpeedTestModal}
        />
      </Modal>
      <ConfirmModal
        open={confirmModalOpen}
        confirmTitle="The test will be canceled."
        onClose={handleToggleConfirmModal}
        confirm={confirmModalCallback}
      />
    </>
  );
};

export default memo(ProfileInformation);
