import { Dispatch } from 'react';

const checks = {
  downlink: {
    median: {
      warning: 5,
      danger: 0.5,
    },
    slowest: {
      warning: 2,
      danger: 0.26,
    },
  },
  uplink: {
    median: {
      warning: 5,
      danger: 0.26,
    },
    slowest: {
      warning: 2,
      danger: 0.13,
    },
  },
  latency: {
    median: {
      warning: 300,
      danger: 1500,
    },
    slowest: {
      warning: 600,
      danger: 3000,
    },
  },
};

const checkResults = (
  type: ChartsNameType,
  results: ResultType[],
) => {
  let status = 'success';

  results.forEach((result) => {
    if (status === 'danger') {
      return;
    }

    if (type === 'downlink' || type === 'uplink') {
      if (result.max_size !== 10240) {
        return;
      }

      if (
        result.median < checks[type].median.danger
          || result.slowest < checks[type].slowest.danger
      ) {
        status = 'danger';
      } else if (
        result.median < checks[type].median.warning
          || result.slowest < checks[type].slowest.warning
      ) {
        status = 'warning';
      }
    }

    if (type === 'latency') {
      if (
        result.median > checks[type].median.danger
        || result.slowest > checks[type].slowest.danger
      ) {
        status = 'danger';
      } else if (
        result.median > checks[type].median.warning
        || result.slowest > checks[type].slowest.warning
      ) {
        status = 'warning';
      }
    }
  });

  return status;
};

const getCommonResult = (charts: ChartsDataType[]) => {
  if (charts.find(({ result }) => result === 'danger')) {
    return 'danger';
  }

  if (charts.find(({ result }) => result === 'warning')) {
    return 'warning';
  }

  return 'success';
};

interface MessageType {
  [key: string]: { [key: string]: string} | string,
}

export const messages: MessageType = {
  downlink: {
    success: '',
    warning: 'less than 5 Mbit/sec',
    danger: 'less than 0.5 Mbit/sec',
  },
  uplink: {
    success: '',
    warning: 'less than 5 Mbit/sec',
    danger: 'less than 0.26 Mbit/sec',
  },
  latency: {
    success: '',
    warning: 'more than 300 ms',
    danger: 'more than 1500 ms',
  },
  success: 'Your internet connection is excellent.',
  warning: 'Your internet connection needs improvement. In case of disconnections please set up LQ.',
  danger: 'Your internet connection is unsatisfactory. Numerous disconnections might take place during the lessons.',
};

type ChartsNameType = 'downlink' | 'uplink' | 'latency';

export interface ResultType {
  max_size: number,
  median: number,
  slowest: number,
}

export interface ChartsDataType {
  completed: boolean;
  progress: number;
  result: string;
  results: ResultType[];
}

export interface InternetSpeedStateType {
  charts: {
    [key: string]: ChartsDataType;
  };
  commonResult: string;
}

export interface InternetSpeedActionType1 {
  type: string,
  chartType: ChartsNameType,
  progress?: number,
  result?: ResultType,
}

export interface ChangeProgressActionType {
  type: typeof CHANGE_PROGRESS,
  chartType: ChartsNameType,
  progress: number,
}

export interface PushResultsActionType {
  type: typeof PUSH_RESULTS,
  chartType: ChartsNameType,
  result: ResultType,
}

export interface ChangeResultActionType {
  type: typeof CHANGE_RESULT,
  chartType: ChartsNameType,
}

export interface ChangeCommonResultActionType {
  type: typeof CHANGE_COMMON_RESULT,
}

type InternetSpeedActionType = (
  ChangeProgressActionType
  | PushResultsActionType
  | ChangeResultActionType
  | ChangeCommonResultActionType
);

export const initialState: InternetSpeedStateType = {
  charts: {
    downlink: {
      completed: false, progress: 0, result: '', results: [],
    },
    uplink: {
      completed: false, progress: 0, result: '', results: [],
    },
    latency: {
      completed: false, progress: 0, result: '', results: [],
    },
  },
  commonResult: '',
};

const CHANGE_PROGRESS = 'CHANGE_PROGRESS';
const PUSH_RESULTS = 'PUSH_RESULTS';
const CHANGE_RESULT = 'CHANGE_RESULT';
const CHANGE_COMMON_RESULT = 'CHANGE_COMMON_RESULT';

export const reducer = (
  state: InternetSpeedStateType,
  action: InternetSpeedActionType,
): InternetSpeedStateType => {
  switch (action.type) {
    case CHANGE_PROGRESS: {
      return {
        ...state,
        charts: {
          ...state.charts,
          [action.chartType]: {
            ...state.charts[action.chartType],
            progress: action.progress,
            completed: action.progress === 1,
          },
        },
      };
    }
    case PUSH_RESULTS: {
      return {
        ...state,
        charts: {
          ...state.charts,
          [action.chartType]: {
            ...state.charts[action.chartType],
            results: [...state.charts[action.chartType].results, action.result],
          },
        },
      };
    }
    case CHANGE_RESULT: {
      return {
        ...state,
        charts: {
          ...state.charts,
          [action.chartType]: {
            ...state.charts[action.chartType],
            result: checkResults(action.chartType, state.charts[action.chartType].results),
          },
        },
      };
    }
    case CHANGE_COMMON_RESULT: {
      return {
        ...state,
        commonResult: getCommonResult(Object.values(state.charts)),
      };
    }
    default: return state;
  }
};

export interface CallBackTypes {
  results(
    test: { type: ChartsNameType, max_size: number },
    result: ResultType,
  ): void,
  stopped(complete): void,
  progress(
    test: { type: ChartsNameType },
    progress: { type_progress: number },
  ): void,
}

export const st_callback = (dispatch: Dispatch<InternetSpeedActionType>): CallBackTypes => {
  const completedCharts: string[] = [];

  return {
    results(test, result) {
      dispatch({
        type: PUSH_RESULTS,
        chartType: test.type,
        result: {
          ...result,
          max_size: test.max_size,
        },
      });

      if (completedCharts.includes(test.type)) {
        dispatch({
          type: CHANGE_RESULT,
          chartType: test.type,
        });
      }
    },
    stopped(complete) {
      if (complete) {
        dispatch({ type: CHANGE_COMMON_RESULT });
      }
    },
    progress(test, progress) {
      if (progress.type_progress === 100) {
        completedCharts.push(test.type);
      }

      dispatch({
        type: CHANGE_PROGRESS,
        chartType: test.type,
        progress: progress.type_progress / 100,
      });
    },
  };
};
