import { AppStateType } from 'types';
import AccountState from 'types/accountType';

export const getAccountInfoLoading = (state: AppStateType): boolean => (
  state.account.userLoading
  || state.account.teacherLoading
  || state.account.regionLoading
  || state.account.topLoading
  || state.account.healthHistoryLoading
  || state.account.balanceHistoryLoading
  || state.account.timezoneLoading
  || state.account.changeUserInfoPending
);

export const getHealthHistoryLoading = (state: AppStateType): boolean => (
  state.account.healthHistoryLoading
);

export const getBalanceHistoryLoading = (state: AppStateType): boolean => (
  state.account.balanceHistoryLoading
);

export const getAccountInfo = (state: AppStateType): AccountState => state.account;
