import React, {
  FC,
  useReducer,
  useEffect,
  useState,
  useCallback,
} from 'react';
import classnames from 'classnames/bind';

import Button from 'components/Button';
import speedTestImg from 'assets/run-speed-test.svg';
import CircleProgress from 'components/CircleProgress';
import {
  messages,
  initialState,
  reducer,
  st_callback,
  ChartsDataType,
} from './InternetSpeedTestHelpers';

import styles from './styles.module.scss';

const cx = classnames.bind(styles);

interface PropsType {
  toggleRunningTest: () => void;
  finishTest: () => void;
}

const InternetSpeedTest: FC<PropsType> = (props) => {
  const {
    toggleRunningTest,
    finishTest,
  } = props;

  const [state, dispatch] = useReducer(reducer, initialState);
  const [testScreen, setTestScreen] = useState(false);

  const handleClick = useCallback(() => {
    if (testScreen) {
      finishTest();
      return;
    }

    setTestScreen(true);
    window.speedtest.start(st_callback(dispatch), '/public/up.html');
    toggleRunningTest();
  }, [testScreen, finishTest, toggleRunningTest]);

  useEffect(() => {
    return () => {
      window.speedtest.stop();
    };
  }, []);

  useEffect(() => {
    if (state.commonResult) {
      toggleRunningTest();
    }
  }, [state.commonResult, toggleRunningTest]);

  let buttonText = '';

  if (state.commonResult) {
    buttonText = 'Finish test';
  } else if (testScreen) {
    buttonText = 'Stop test';
  } else {
    buttonText = 'Run speed test';
  }

  return (
    <div className={cx('internetSpeedTest')}>
      {testScreen ? (
        <div className={cx('chartContainer')}>
          {Object.entries(state.charts as {[key: string]: ChartsDataType}).map(
            ([type, { progress, result, completed }]: [string, ChartsDataType]) => {
              return (
                <div className={cx('chart')} key={type}>
                  <CircleProgress
                    value={progress}
                    type={progress ? 'testing' : 'waiting'}
                    result={result}
                    completed={completed}
                  />
                  <span className={cx('chartName')}>{type}</span>
                  <b>{state.commonResult && messages[type][result]}</b>
                </div>
              );
            },
          )}
        </div>
      ) : (
        <div className={cx('container')}>
          <img src={speedTestImg} alt="internet speed test" />
        </div>
      )}
      <div className={cx('commonResult')}>
        <b>{messages[state.commonResult]}</b>
      </div>
      <div className={cx('control')}>
        <Button
          theme="primary"
          text={buttonText}
          className={cx('button')}
          onClick={handleClick}
        />
      </div>
    </div>
  );
};

export default InternetSpeedTest;
