import React, { FC, memo } from 'react';
import classnames from 'classnames/bind';
import { FaHeart, FaHeartBroken, FaQuestionCircle } from 'react-icons/fa';
import Tooltip from 'components/Tooltip';

import AccountHealthTooltip from './AccountHealthTooltip';

import styles from './styles.module.scss';

const cx = classnames.bind(styles);

export interface PropsType {
  heartsLength?: number;
}

const AccountHealth: FC<PropsType> = ({ heartsLength = 0 }) => {
  const length = heartsLength > 10 ? 10 : heartsLength;
  const Hearts: number[] = Array.from(Array(length).keys());
  const BrokenHearts: number[] = Array.from(Array(10 - length).keys());

  return (
    <div className={cx('accountHealth')}>
      {Hearts.map((ind) => (
        <FaHeart
          className={cx('fillHealth')}
          color="#EB5757"
          key={ind}
        />
      ))}
      {BrokenHearts.map((ind) => (
        <FaHeartBroken
          className={cx('emptyHealth')}
          color="#bbb"
          key={ind}
        />
      ))}
      <Tooltip title={<AccountHealthTooltip className={cx('accountHealthTooltip')} />}>
        <span>
          <FaQuestionCircle />
        </span>
      </Tooltip>
    </div>
  );
};

export default memo(AccountHealth);
