import React, {
  FC,
  useState,
  useEffect,
  ChangeEvent,
  SyntheticEvent,
} from 'react';
import InputAdornment from '@material-ui/core/InputAdornment';
import IconButton from '@material-ui/core/IconButton';
import { FaEyeSlash, FaEye } from 'react-icons/fa';
import classnames from 'classnames/bind';
import Modal from 'components/Modal';
import TextField from '@material-ui/core/TextField';
import Button from 'components/Button';
import validate, { ValidatorNames } from 'utils/validation';
import { FormStateType, FormFieldType, ValidationRuleType } from 'types/common';
import passwordGenerator from 'generate-password';

import styles from './styles.module.scss';

const cx = classnames.bind(styles);

export interface PropsType {
  isOpen: boolean,
  onClose: () => void,
}

const ChangePasswordModal: FC<PropsType> = ({ onClose, isOpen }) => {
  const [formIsValid, setFormIsValid] = useState(false);
  const [submitBtnClicked, setSubmitBtnClicked] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const handleClickOnShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const fields = {
    password: 'password',
    repeatPassword: 'repeatPassword',
  };

  const [formData, setFormData] = useState<FormStateType>({
    [fields.password]: {
      value: '',
      validationMessage: null,
    },
    [fields.repeatPassword]: {
      value: '',
      validationMessage: null,
    },
  });

  const validation: { [key: string]: ValidationRuleType[] } = {
    [fields.password]: [
      { type: ValidatorNames.required },
      {
        type: ValidatorNames.minLength,
        length: 8,
      },
    ],
    [fields.repeatPassword]: [
      { type: ValidatorNames.required },
      {
        type: ValidatorNames.equals,
        compareTo: formData[fields.password].value,
      },
    ],
  };

  const onFormDataChange = (fieldName: string) => (event: ChangeEvent<HTMLInputElement>) => {
    setFormData({
      ...formData,
      [fieldName]: {
        value: event.target.value,
        validationMessage: validate(validation[fieldName], event.target.value),
      },
    });
  };

  const handleSubmit = (event: SyntheticEvent) => {
    event.preventDefault();
    let valid = true;

    if (!submitBtnClicked) {
      const newFormData = {};

      for (const key in fields) {
        const validRes = validate(validation[key], formData[key].value);

        newFormData[key] = {
          ...formData[key],
          validationMessage: validRes,
        };

        if (validRes) {
          valid = false;
        }
      }

      setFormData(newFormData);

      setSubmitBtnClicked(true);
    }

    if (formIsValid && valid) {
      // dispatch(loginRequest({
      //   password: formData[fields.password].value,
      // }));
    }
  };

  useEffect(() => {
    let valid = true;

    for (const index in formData) {
      if (formData[index].validationMessage) {
        valid = false;
        break;
      }
    }

    if (formIsValid !== valid) {
      setFormIsValid(valid);
    }
  }, [formData]);

  const getFieldProps = (fieldName: string) => {
    const fieldProps: FormFieldType = {
      onChange: onFormDataChange(fieldName),
      value: formData[fieldName].value,
      hasError: false,
      helperText: null,
    };

    if (submitBtnClicked) {
      fieldProps.hasError = !!formData[fieldName].validationMessage;
      fieldProps.helperText = formData[fieldName].validationMessage;
    }

    return fieldProps;
  };

  const handleGeneratePassword = () => {
    const password = passwordGenerator.generate({
      length: 8,
      numbers: true,
    });

    setFormData({
      ...formData,
      [fields.password]: {
        value: password,
        validationMessage: null,
      },
      [fields.repeatPassword]: {
        value: password,
        validationMessage: null,
      },
    });

    setShowPassword(true);
  };

  const passwordProps = getFieldProps(fields.password);
  const repeatPasswordProps = getFieldProps(fields.repeatPassword);

  return (
    <Modal
      title="Permanent Password"
      onClose={onClose}
      open={isOpen}
      className={cx('changePassword')}
    >
      <form
        onSubmit={handleSubmit}
        noValidate
        autoComplete="off"
      >
        <div className={cx('info')}>
          A permanent password will let you log in quickly and easily
        </div>
        <TextField
          className={cx({ hasError: passwordProps.hasError })}
          label="New password"
          variant="outlined"
          type={showPassword ? 'text' : 'password'}
          size="small"
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <IconButton onClick={handleClickOnShowPassword}>
                  {showPassword ? <FaEye /> : <FaEyeSlash />}
                </IconButton>
              </InputAdornment>
            ),
          }}
          required
          fullWidth
          {...passwordProps}
        />
        <TextField
          className={cx({ hasError: repeatPasswordProps.hasError })}
          label="Repeat the new password"
          variant="outlined"
          type={showPassword ? 'text' : 'password'}
          size="small"
          required
          fullWidth
          {...repeatPasswordProps}
        />
        <div className={cx('footer')}>
          <button
            className={cx('generatePassword')}
            onClick={handleGeneratePassword}
          >
            Generate password
          </button>
          <Button
            theme="primary"
            text="Change"
            className={cx('button')}
            type="submit"
          />
        </div>
      </form>
    </Modal>
  );
};

export default ChangePasswordModal;
