import React, { FC, memo } from 'react';
import classnames from 'classnames/bind';
import { FaCheck } from 'react-icons/fa';

import styles from './styles.module.scss';

const cx = classnames.bind(styles);

interface PropsType {
  value: number;
  type: string;
  result: string;
  completed: boolean;
}

const CircleProgress: FC<PropsType> = (props) => {
  const {
    value = 1,
    type,
    result,
    completed,
  } = props;

  const pct = (1 - value) * Math.PI * 100;

  return (
    <div>
      <div className={cx('circleProgress')}>
        <div className={cx('wrap')}>
          <svg
            width={100}
            height={100}
            viewBox="0 0 100 100"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            className={cx('circle')}
          >
            <circle
              className={cx('background', {
                [`${result}`]: !!result && completed,
              })}
              r="50"
              cx="50"
              cy="50"
              fill="transparent"
            />
            <circle
              className={cx('bar', {
                [`${result}`]: !!result && completed,
              })}
              r="50"
              cx="50"
              cy="50"
              fill="transparent"
              strokeDasharray={Math.PI * 100}
              strokeDashoffset={pct}
            />
          </svg>
          <div className={cx('text')}>
            {result ? (
              <FaCheck color="#fff" size={50} />
            ) : (
              <>
                {type}
                <br />
                {Math.ceil(value * 100)}
                {' '}
                %
              </>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default memo(CircleProgress);
