import React, { FC, memo, useState } from 'react';
import classnames from 'classnames/bind';
import { useSelector } from 'react-redux';
import { AiFillHeart } from 'react-icons/ai';

import SectionTitle from 'components/SectionTitle';
import Button from 'components/Button';

import { HealthType } from 'types/common';
import { getHealthHistoryLoading } from 'store/account/selectors';
import styles from './styles.module.scss';

const cx = classnames.bind(styles);

export interface PropsType {
  historyData: HealthType[],
}

const HealthHistory: FC<PropsType> = ({ historyData }) => {
  const isLoading: boolean = useSelector(getHealthHistoryLoading);
  const moreAmount = 20;
  const [amountOnPage, setAmountOnPage] = useState(moreAmount);

  const visibleHistory = [...historyData];
  visibleHistory.length = amountOnPage > historyData.length ? historyData.length : amountOnPage;

  const handleShowMore = () => {
    setAmountOnPage(amountOnPage + moreAmount);
  };

  return (
    <div className={cx('healthHistory')}>
      <SectionTitle title="Account health history" loading={isLoading} bold />
      <table>
        <thead>
          <th>Date</th>
          <th>Description</th>
          <th className={cx('heart')}>
            Hearts
            <AiFillHeart color="#EB5757" />
          </th>
        </thead>
        <tbody>
          {visibleHistory.map((item) => (
            <tr key={item.id}>
              <td>
                {new Date(item.created).toLocaleDateString('en-US')}
              </td>
              <td>{item.description}</td>
              <td className={cx('heart')}>{parseInt(item.actual_change, 10)}</td>
            </tr>
          ))}
        </tbody>
      </table>
      {historyData.length > amountOnPage && (
        <Button text="Further classes" onClick={handleShowMore} />
      )}
    </div>
  );
};

export default memo(HealthHistory);
