import React, { FC, useEffect } from 'react';
import classnames from 'classnames/bind';
import { Helmet } from 'react-helmet';
import { useSelector, useDispatch } from 'react-redux';

import { accountPending } from 'store/account/actions';

import TopLoading from 'components/TopLoading';
import Container from 'components/Container';
import { getAccountInfo, getAccountInfoLoading } from 'store/account/selectors';
import ProfileInformation from './ProfileInformation';

import styles from './styles.module.scss';
import BalanceHistory from './BalanceHistory';
import HealthHistory from './HealthHistory';

const cx = classnames.bind(styles);

const Account: FC = () => {
  const dispatch = useDispatch();

  const {
    balanceHistory,
    healthHistory,
  } = useSelector(getAccountInfo);
  const isLoading = useSelector(getAccountInfoLoading);

  useEffect(() => {
    dispatch(accountPending());
  }, [dispatch]);

  return (
    <>
      <Helmet>
        <title>Novakid - Account</title>
      </Helmet>
      <TopLoading loading={isLoading} />
      <div className={cx('account')}>
        <Container>
          <ProfileInformation />
          <div className={cx('histories')}>
            <BalanceHistory historyData={balanceHistory} />
            <HealthHistory historyData={healthHistory.filter((h) => h.actual_change !== '0.00')} />
          </div>
        </Container>
      </div>
    </>
  );
};

export default Account;
