import React, { ReactNode, FC } from 'react';

interface TabPanelProps {
  children: ReactNode;
  id: number;
  selectedId: number;
}

const TabPanel: FC<TabPanelProps> = (props) => {
  const {
    children,
    selectedId,
    id,
  } = props;

  return (
    <div
      role="tabpanel"
      hidden={selectedId !== id}
      id={`full-width-tabpanel-${id}`}
      aria-labelledby={`full-width-tab-${id}`}
    >
      {id === selectedId && (
        <div>{children}</div>
      )}
    </div>
  );
};

export default TabPanel;
