import React, { FC, memo, useState } from 'react';
import classnames from 'classnames/bind';
import { useSelector } from 'react-redux';
import { BiCheckCircle } from 'react-icons/bi';

import SectionTitle from 'components/SectionTitle';
import Button from 'components/Button';

import { TransactionType } from 'types/common';
import { getBalanceHistoryLoading } from 'store/account/selectors';
import styles from './styles.module.scss';

const cx = classnames.bind(styles);

export interface PropsType {
  historyData: TransactionType[],
}

const BalanceHistory: FC<PropsType> = ({ historyData }) => {
  const isLoading: boolean = useSelector(getBalanceHistoryLoading);
  const moreAmount = 20;
  const [amountOnPage, setAmountOnPage] = useState(moreAmount);

  const visibleHistory = [...historyData];
  visibleHistory.length = amountOnPage > historyData.length ? historyData.length : amountOnPage;

  const handleShowMore = () => {
    setAmountOnPage(amountOnPage + moreAmount);
  };

  return (
    <div className={cx('balanceHistory')}>
      <SectionTitle title="Balance history" loading={isLoading} bold />
      <table>
        <thead>
          <th> </th>
          <th>Date</th>
          <th>Description</th>
        </thead>
        <tbody>
          {visibleHistory.map((item) => (
            <tr key={item.id}>
              <td>
                <BiCheckCircle size="24" color="#5cb85c" />
              </td>
              <td>
                {new Date(item.created).toLocaleString('en-US')}
              </td>
              <td>{item.description}</td>
              <td className={cx('amount')}>
                {item.value}
                {' '}
                classes
              </td>
            </tr>
          ))}
        </tbody>
      </table>
      {historyData.length > amountOnPage && (
        <Button text="Further classes" onClick={handleShowMore} />
      )}
    </div>
  );
};

export default memo(BalanceHistory);
